body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Montserrat', 'Rubik', 'Spartan',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.hero-section .nav-section img{
  width: 260px;
  margin-left: 60px;
}
.hero-section .nav-section .logo-section{
  padding: 0px !important;
  background-color: #00436d;
}
.hero-section .nav-section nav{
  float: right;
  height: 100%;
  display: flex;
  align-items: center;
}
.hero-section .nav-section .nav-container{
  padding: 0px !important;
}
.hero-section .nav-section nav a{
  margin-left: 12px;
  margin-right: 12px;
  text-transform: capitalize;
}
.hero-section .nav-section nav a:hover{
  color: #ee9f1e;
  text-decoration: none;
}
.hero-section .nav-section nav .nav-decoration{
  width: 48px;
  height: 6px;
  display: inline-block;
  background-color: #ee9f1e;
  margin-left: 24px;
}
.hero-section-1{
  background-color: #00436d;
  height: 580px;
}
.hero-section-1 img{
  width: 650px;
}
.hero-image-border-top{
  display: block;  
  margin-top: 40px;
  width: 150px;
  height: 10px;
  background-color: #ee9f1e;
}
.hero-image-border-bottom-block{
  display: block;
  width: 650px;
}
.hero-image-border-bottom{
  display: block;  
  float: right;
  width: 250px;
  height: 10px;
  background-color: #ee9f1e;
}
.hero-section-2 section{
  margin-top: 40px;
  width: 90%;
  float: right;
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  margin-bottom: 14px;
}
.hero-section-2 section h1{
  color: #00436d;
  text-transform: capitalize;
  font-weight: bold;
}
.hero-section-2 section h2{
  background-color: #ee9f1e;
  color: white;
  width: 70%;
  margin: auto;
  padding-top: 8px;
  padding-bottom: 8px;
  font-weight: bold;
}
.hero-section-2 section button{
  border: none;
  border-radius: 0px;
  background-color: #00436d;
  font-size: 42px;
  width: 60%;
  margin-top: 60px;
  margin-bottom: 60px;
}
.hero-section-2 section button:hover,
.hero-section-2 section button:active,
.hero-section-2 section button:focus{
  background-color: #0f70a3 !important;
}
.hero-section-2 section .hero-country-big{
  display: inline-block;
  width: 100px;
  color: white;
  font-weight: bold;
  font-family: "Spartan";
  font-size: 18px;
  padding-top: 4px;
  margin-left: 12px;
  margin-right: 12px;
  cursor: pointer;
}
.hero-section-2 section .hero-country-big.color-one,
.background-color-one{
  background-color: #0d5a64;
}
.hero-section-2 section .hero-country-big.color-two,
.background-color-two{
  background-color: #0f70a3;
}
.hero-section-2 section .hero-country-big.color-three,
.background-color-three{
  background-color: #ea015d;
}
.hero-section-2 section .hero-country-big.color-four,
.background-color-four{
  background-color: #000000;
}
.hero-section-2 section .hero-country-small{
  display: inline-block;
  margin-left: 9px;
  margin-right: 9px;
}
.hero-section-2 section .hero-country-small-contain{
  margin-top: 12px;
  color: #0f70a3;
}
.hero-filiere-contain{
  z-index: 1000;
  margin-top: -72px !important;
}
.hero-filiere-contain .hero-filiere{
  padding-left: auto;
  padding-right: auto;
  width: 210px;
  word-wrap: break-word !important;
  -webkit-clip-path: polygon(75% 0%, 90% 50%, 75% 100%, 0% 100%, 15% 50%, 0% 0%);
  clip-path: polygon(75% 0%, 90% 50%, 75% 100%, 0% 100%, 15% 50%, 0% 0%);
  display: inline-block;
  color: white;
  text-align: center;
  padding-top: 24px;
  padding-bottom: 24px;
  cursor: pointer;
  margin: auto;
}
.hero-filiere-contain .hero-filiere-decoration{
  width: 50px;
  -webkit-clip-path: polygon(42% 0, 92% 50%, 42% 100%, 0% 100%, 50% 50%, 0% 0%);
clip-path: polygon(42% 0, 92% 50%, 42% 100%, 0% 100%, 50% 50%, 0% 0%);
}
.statistic-section{
  background-color: #ee9f1e;
  padding-top: 48px;
  padding-bottom: 24px;
}
.statistic-section .statistic-element{
  text-align: center;
  color: white;
}
.statistic-section .statistic-element h3{
  font-family: "Spartan";
  font-size: 3em;
  font-weight: bold;
}
.statistic-section .statistic-element p{
  font-family: "Spartan";
  text-transform: capitalize;
}
.service-section h2{
  width: 100%;
  text-align: center;
  font-weight: bold;
  margin-top: 80px;
  margin-bottom: 64px;
}
.service-element{
  display: flex;
  flex-direction: row;
  margin-top: 6px;
  margin-bottom: 24px;
}
.service-element .service-element-index{
  display: inline-block;
  border: 2px solid #00436d !important;
  color: #00436d;
  padding: 8px;
  padding-bottom: auto;
  height: 40px;
  font-weight: bold;
  margin-top: 6px;
}
.service-element .service-element-contain{
  display: flex;
  flex-direction: column;
  padding-left: 8px;
}
.service-element .service-element-contain .service-element-title{
  font-weight: bold;
  color: #00436d;
  font-size: 18px;
  margin-bottom: 8px;
}
.service-element .service-element-contain .service-element-description{
  text-align: left;
  font-family: "Spartan";
}
.testimony-section{
  margin-top: 40px;  
  padding-top: 40px;
  padding-bottom: 40px;
  background-image: url("./images/testimony.jpg") !important;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: #00436dCC;
}
.testimony-section h2{
  width: 100%;
  text-align: center;
  margin-bottom: 64px;
  font-weight: bold;
  color: white;
}
.testimony-element{
  display: flex;
  flex-direction: row;
}
.testimony-element .testimony-element-contain{
  display: flex;
  flex-direction: column;
  background-color: #FFFFFFAA;
  padding: 8px;
}
.testimony-element .testimony-element-contain .testimony-element-title{
  color: #00436d;
  font-family: "Spartan";
  height: 120px;
  margin-bottom: 16px;
}
.partner-section{
  margin-top: 40px;
}
.partner-section .partner-carousel-container ul{
  margin: auto !important;
}
.partner-section .partner-carousel-container ul li img{
  margin: auto !important;
}
.partner-section li.partner-carousel-item{
  display: flex;
  justify-content: center;
}
.bold{
  font-weight: bold;
  font-size: 18px;
}
.highlight{
  color: #00436d;
  font-weight: bold;
}
.footer-section{
  background-color: #00436d;
}
.footer-section .footer-logo{
  height: 140px;
  margin-bottom: 18px;
}
.footer-section .footer-section-one{
  padding-left: 32px;
}
.footer-section .footer-section-one p{
  margin-bottom: 8px !important;
  color: white;
  opacity: 0.8;
  font-size: 14px;
}
.footer-section .footer-section-one span.bold{
  font-size: 14px;
}
.footer-section .footer-section-two{
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.footer-section .footer-section-two nav{
  display: flex;
  flex-direction: row;
  margin-top: 40px;
  align-content: center;
  justify-content: center;
}
.footer-section .footer-section-two .footer-section-two-infos{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.footer-section .footer-section-two .footer-section-two-infos a{
  width: 40%;
}
.footer-section .footer-section-two .footer-section-two-infos button{
  width: 100%;
  border: none;
  border-radius: 0px;
  height: 80px;
  font-size: 22px;
  background-color: #ee9f1e;
  margin-top: 32px;
  margin-bottom: 24px;
}
.footer-section .footer-section-two .footer-section-two-infos i{
  color: #FFFFFF;
  font-size: 24px;
  cursor: pointer;
  margin-right: 12px;
  margin-left: 12px;
}
.footer-section .footer-section-two .footer-section-two-copyright{
  display: flex;
  justify-content: center;
  margin-top: 48px !important;
  font-size: 12px;
  color: white;
  opacity: 0.8;
}
.footer-section .footer-section-two .footer-section-two-copyright a{
  color: white;
}
.footer-section .footer-section-two nav a{
  display: block;
  text-transform: capitalize;
  margin-top: 7px;
  margin-bottom: 7px;
  color: white;
  opacity: 0.8;
  margin-right: 24px;
  margin-left: 24px;
}
.section-apropos h1{
  font-weight: bold;
  text-transform: capitalize;
  color: #00436d;
  margin-top: 48px;
  margin-bottom: 24px;
}
.section-apropos p{
  text-align: justify;
  padding-right: 24px;
}
.filiere-section{
  width: 100%;
}
.filiere-section h1{
  color: #00436d;
  font-weight: bold;
  margin-top: 48px;
  margin-bottom: 48px;
  margin-left: 40px;
}
.filiere-title ul{
  width: 100% !important;
  list-style: none;
  margin-left: 0px;
}
.filiere-title ul li{
  display: block;
  padding: 12px;
  text-align: right;
  background-color: #ee9f1e;
  color: white;
  margin-top: 8px;
  margin-bottom: 8px;
  cursor: pointer;
  font-weight: bold;
  border-bottom-right-radius: 16px;
}
.filiere-title ul li:hover,
.filiere-title ul li.active{
  background-color: #00436d;
}
.filiere-description{
  background-color: #00436d;
  display: flex;
  flex-direction: column;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  margin-bottom: 16px !important;
  margin-top: 16px !important;
  border-left-color: #ee9f1e;
  border-left-style: solid;
  border-left-width: 8px;
  border-top-color: #ee9f1e;
  border-top-style: solid;
  border-top-width: 8px;
  border-bottom-color: #ee9f1e;
  border-bottom-style: solid;
  border-bottom-width: 8px;
}
.filiere-description .image-container{
  height: 310px;
  overflow: hidden;
}
.filiere-description ul{
  width: 48%;
  margin-right: 2%;
  list-style: square;
  margin-top: 16px;
  z-index: 16;  
}
.filiere-description ul li{
  color: white;
  margin-bottom: 8px;
  font-weight: bold;
}